import React, {useEffect, useState} from 'react';
import { useHistory,useParams } from 'react-router-dom';
import styled from 'styled-components';

import './Detail.scss'

let 박스 = styled.div`
 padding:20px;
 background:black;
 font-size:25px;
`;
let 제목=styled.h4`
color:red;
font-size:25px;
text-align:center;
color:${props=>props.색상}
`;


class Details extends React.Component{
  //컴포넌트가실행될떄
  componentDidMount(){

  }
  //컴포넌트가사라질떄
  componentWillUnmount(){

  }
}





function Detail(a){
   
 let history=useHistory();
  let {id}=useParams();
  console.log(id);
  let ids=Number(id)+1;
  console.log(ids);
 let [상세,상세변경]=useState(true);
  let [값,값변경]=useState('');
  console.log(상세);
  useEffect(()=>{
   let 타이머= setTimeout(()=>{ 상세변경(false)},2000);
   console.log('gd')
  return function(){
    clearTimeout(타이머);
  }
    //  unmiount 할떄사용 return function 어쩌구(){실행코드}
  },[상세]);
  console.log(값);
    return(
<div className="container">
      <박스>
      <제목 색상='orange'>상세</제목>
        </박스>
        <input onChange={(e)=>{값변경(e.target.value)}} />


        
        {
        상세==true
        ? <div className="my-alert">g하이하이</div>
        :null
        }
      <div className="row">
        <div className="col-md-6">
          <img src={'https://codingapple1.github.io/shop/shoes'+ids+'.jpg'} width="100%" />
        
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{a.items[id].title}</h4>
          <p>{a.items[id].content}</p>
          <p>{a.items[id].price}</p>
          <button className="btn btn-danger" onClick={()=>{}}>주문하기</button> 
          <button className="btn btn-danger" onClick={()=>{history.push('/');}}>뒤로가기</button> 
         
          <Info 재고={a.재고} id={id}/>
        </div>
      </div>
</div> 

    );
}


function Info(a){
  return(
    <p>재고 :{a.재고[a.id]}</p>
  )
}
export default Detail;